import React, { Component } from "react";
import { connect } from "react-redux";
import Switch from "react-switch";
import { Field, reduxForm } from "redux-form";
import { getJenisLabel } from "./actions/pengaturan";
import { warning } from "./Alert";
import { renderFieldSelect } from "./renderField";

class Pengaturan extends Component {
  constructor(props) {
    super(props);
    this.state = {
      timbangan: localStorage.getItem("enableTimbangan"),
    };
  }
  handleChange(e) {
    this.setState({
      timbangan: e,
    });
  }
  setTimbang(e) {
    var value = e.target.value;
    this.setState({
      namaTimbangan: value.toUpperCase(),
    });
  }
  setPengaturan() {
    if (this.state.namaTimbangan === undefined) {
      warning("Mohon isi Nama Timbangan");
      return false;
    }
    localStorage.setItem(
      "namaTimbangan",
      this.state.namaTimbangan.toLowerCase()
    );
    localStorage.setItem("enableTimbangan", this.state.timbangan);
    window.location.reload();
  }

  componentDidMount() {
    this.props.dispatch(getJenisLabel());
  }

  render() {
    return (
      <div className="container-fluid">
        <div className="col-lg-12">
          <div className="card">
            <div className="card-body">
              <div className="col-lg-12">
                <h1>Pengaturan</h1>
                <div className="row mt-5">
                  <div className="col-lg-6">
                    <h4>Komunikasi Timbangan</h4>
                  </div>
                  <div className="col-lg-6">
                    <div className="text-right">
                      <Switch
                        checked={this.state.timbangan}
                        onChange={(e) => this.handleChange(e)}
                        onColor="#86d3ff"
                        onHandleColor="#2693e6"
                        handleDiameter={30}
                        uncheckedIcon={false}
                        checkedIcon={false}
                        boxShadow="0px 1px 5px rgba(0, 0, 0, 0.6)"
                        activeBoxShadow="0px 0px 1px 10px rgba(0, 0, 0, 0.2)"
                        height={20}
                        width={48}
                        className="react-switch"
                        id="material-switch"
                      />
                    </div>
                  </div>
                  <div className="col-lg-6 mt-3">
                    <h4>Nama Timbangan</h4>
                  </div>
                  <div className="col-lg-6 mt-3">
                    <input
                      type="text"
                      className="form-control"
                      onChange={(e) => this.setTimbang(e)}
                    />
                  </div>
                  <div className="col-lg-6 mt-3">
                    <label htmlFor="">Jenis label</label>
                    <h4 className="text-danger">
                      Label Terpilih : <strong>{this.props.jenis_label}</strong>
                    </h4>
                  </div>
                  <div className="col-lg-6 mt-3">
                    <Field
                      name="jenis_label"
                      component={renderFieldSelect}
                      options={[
                        {
                          value: "Label 1",
                          name: "Label 1",
                        },
                        {
                          value: "Label 2",
                          name: "Label 2",
                        },
                      ]}
                      onChange={(data) => {
                        localStorage.setItem("jenis_label", data);
                        this.props.dispatch(getJenisLabel());
                      }}
                    />
                  </div>
                  <div className="col-lg-12 mt-3">
                    <div className="text-right">
                      <button
                        className="btn btn-primary"
                        onClick={() => this.setPengaturan()}
                      >
                        Simpan
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

Pengaturan = reduxForm({
  form: "Pengaturan",
})(Pengaturan);

export default connect((state) => {
  return {
    jenis_label: state.provinsi.jenis_label,
    initialValues: {
      jenis_label: state.provinsi.jenis_label,
    },
  };
})(Pengaturan);
